/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import { useBlockProps, RichText } from '@wordpress/block-editor';

import { EditorPartialsInspectorControls } from './editor-partials/inspector';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @param  props
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {Element} Element to render.
 */
export default function Edit( props ) {
	const { attributes, setAttributes } = props;

	const { title, excerpt, color } = attributes;

	const onChangeTitle = ( newValue ) => {
		setAttributes( { title: newValue } );
	};

	const onChangeExcerpt = ( newValue ) => {
		setAttributes( { excerpt: newValue } );
	};

	return (
		<>
			<EditorPartialsInspectorControls { ...props } />
			<div { ...useBlockProps() }>
				<div style={ { backgroundColor: attributes.color } }>
					<RichText
						className="wp-block--block-sandbox-simple-modules--title"
						placeholder={ __( 'Callout Title', 'block-sandbox' ) }
						tagName="h2"
						onChange={ onChangeTitle }
						value={ title }
						allowedFormats={ [] }
					/>
					<RichText
						placeholder={ __( 'Excerpt', 'block-sandbox' ) }
						tagName="p"
						classname="wp-block--block-sandbox-simple-modules--excerpt"
						onChange={ onChangeExcerpt }
						value={ excerpt }
						allowedFormats={ [
							'core/italic',
							'core/bold',
							'core/link',
						] }
					/>
				</div>
			</div>
		</>
	);
}
